/* harmony default export */ var App = ({
  name: "app",
  mounted: function mounted() {
    this.init();
    $(".publish-main-list").each(function (index, item) {
      item.bFlag = false;
    });
    $("body").delegate(".publish-main-list", "click", function () {
      if (this.bFlag) {
        $(this).removeClass("select");
        $(this).siblings(".publish-sub-list").css("display", "none");
      } else {
        $(this).addClass("select");
        $(this).siblings(".publish-sub-list").css("display", "block");
      }
      this.bFlag = !this.bFlag;
    });
  },

  methods: {
    init: function init() {
      $(".menu-options").on("click", function () {
        $(this).addClass("selected").siblings().removeClass("selected");
      });
    }
  },
  watch: {
    /*监视路由变化*/
    $route: function $route() {}
  }
});
